.slide-container {
    overflow: hidden;
    width: 100%;
  }
  
  .slide-text {
    position: relative;
    animation: slide 3s ease infinite;
  }
  
  @keyframes slide {
    0% {
      transform: translateX(100%);
    }
    100% {
      transform: translateX(-100%);
    }
  }
  .fade-in {
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
  
  .fade-in:hover {
    opacity: 1;
  }
  